<template>
    <section class="letter">
        <div class="l-title c-center">
            请填写以下内容，*为必填内容
        </div>
        <div class="form-content">
            <section class="line item">
                <div class="s-name">名字 ：
                </div>
                <div class="s-content" style="margin-left: 46px;">
                    <input type="text" v-model="name" placeholder="请输入" class="st-name">
                </div>
            </section>
            <section class="line item">
                <div class="c-row">
                    <div class="c-span3">
                        <div class="s-name">性别 ：
                        </div>
                        <div class="">
                            <span class="choice-select">请选择
                                                                            <i  class="icon iconfont  icon-other_arrow_right"></i>
                                                                           </span>
                            <select v-model="sex">
                                                                                <option disabled value=""></option>
                                                                                <option  value="男">男</option>
                                                                                <option  value="女">女</option>
                                                                            </select>
                        </div>
                    </div>
                    <div class="c-span3">
                        <div class="s-name">年龄 ：
                        </div>
                        <div class="s-content" style="margin-left: 46px;">
                            <input type="text" v-model="age" placeholder="请输入" v-on:keyup="check_count($event)" class="st-name">
                        </div>
                    </div>
    
                </div>
            </section>
            <section class="line item">
                <div class="s-name"><span class="s-ico">*</span>电子邮箱 ：
                </div>
                <div class="s-content">
                    <input type="text" v-model="email" placeholder="请输入" class="st-name">
                </div>
            </section>
            <section class="line item">
                <div class="s-name"><span class="s-ico">*</span>联系电话 ：
                </div>
                <div class="s-content">
                    <input type="text" v-model="tel" placeholder="请输入" class="st-name">
                </div>
            </section>
    
    
        </div>
        <div class="mid-line"></div>
        <div class="form-content">
            <section class="line item">
                <div class="s-name"><span class="s-ico">*</span>信件主题 ：
                </div>
                <div class="s-content">
                    <input type="text" v-model="theme" placeholder="请输入" class="st-name">
                </div>
            </section>
            <section class="item">
                <div class="s-name"><span class="s-ico">*</span>信件主题 ：
                    <span class="choice-select" style="margin-left:-2px; font-size:14px;">(请将信件内容控制在1500字以内)
                                                                                </span>
                </div>
                <div class="ss-content">
                    <textarea v-model="message" placeholder="" class="st-name"></textarea>
    
                </div>
            </section>
        </div>
        <div class="mid-line"></div>
        <div class="form-content">
            <section class="line item">
                <div class="s-name"><span class="s-ico">*</span>验证码：
                </div>
                <div class="s-content">
                    <input class="item-slot st-name" type="text" style="width:80px" v-model="code" placeholder="请输入">
                    <img class="item-slot" width="60" height="32" src="../../../assets/img/home_police.png" alt="">
                </div>
            </section>
        </div>
        <div class="bottom-btn">
            <div class="c-center sub-btn" @click="submit">
                发送邮件
            </div>
        </div>
    
        <Tip :value="tiplock" @visible-change="showTip">{{validateMsg}}</Tip>
    </section>
</template>

<script>
    //  S
    import Tip from './tips';
    export default {
    
        components: {
            Tip
        },
    
        data() {
            return {
                'name': '',
                'sex': '男',
                'age': '',
                'message': '',
                'tel': '',
                'theme': '',
                'email': '',
                'code': '',
                tiplock: false,
                validateMsg: '',
                validateData: null
    
            };
        },
        created() {
           // this.showTip(true);
        },
        methods: {
            loadMore() {
    
            },
            showTip(val) {
                this.tiplock = val;
            },
            submit() {
    
                this.validateData = {
                    email: this.email,
                    tel: this.tel,
                    theme: this.theme,
                    message: this.message
                }
    
                for (var key in this.validateData) {
                    if (!this.validate(this.validateData[key], key)) {
                        return false;
                    }
                }
                this.validateData.code = this.code;
                this.validateData.name = this.name;
                this.validateData.age = this.age;
                this.validateData.sexy = this.sex;
                console.log(this.validateData);
            },
            validate(val, type) {
                let mail = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
                let tel = /^1\d{10}$/;
                let space = /^\s*$/;
    
                if (space.test(val) && type === 'message') {
                    this.validateMsg = '内容不能为空~';
                    this.showTip(true);
                    return false;
                }
                if (space.test(val) && type === 'tel') {
                    this.validateMsg = '手机号不能为空~';
                    this.showTip(true);
                    return false;
                }
                if (space.test(val) && type === 'email') {
                    this.validateMsg = '邮箱不能为空~';
                    this.showTip(true);
                    return false;
                }
                if (space.test(val) && type === 'theme') {
                    this.validateMsg = '主题不能为空~';
                    this.showTip(true);
                    return false;
                }
                if (!mail.test(val) && type === 'email') {
                    this.validateMsg = '您输入邮箱不合法~';
                    this.showTip(true);
                    return false;
                }
                if (!tel.test(val) && type === 'tel') {
                    this.validateMsg = '您输入手机号不合法~';
                    this.showTip(true);
                    return false;
                }
    
                if (val.length > 1500 && type === 'message') {
                    this.validateMsg = '内容不能过长~';
                    this.showTip(true);
                    return false;
                }
                return true;
            },
            check_count(event) {
                return false;
                if (event) {
                    let value = event.target.value;
                    if (!/^\+?[1-9][0-9]*$/.test(value)) {
                        alert('只能正整数');
                        event.target.value = '';
                    }
                }
    
    
            }
        }
    };
</script>

<style scoped lang='less'>
        .letter {
            
        position: relative;
        padding-bottom:65px;
        .mid-line {
            height: 12.5px;
            background: #f5f5f5;
        }
        .s-ico {
            float: left;
            color: #fe2f2f;
        }
        .item-slot {
            float: left;
            margin-left: 8px;
            margin-top: 3.5px;
        }
        input::-webkit-input-placeholder {
            color: #b2b2b2!important;
            font-size: 14px;
        }
        textarea::-webkit-input-placeholder {
            color: #b2b2b2!important;
            font-size: 14px;
        }
        .l-title {
            background: #fd8948;
            font-size: 14px;
            color: #fff;
            line-height: 40px;
        }
        .choice-select {
            font-size: 14px;
            line-height: 40px;
            color: #b2b2b2;
            i {
                font-size: 12px;
            }
        }
        select {
            width: 46px;
        }
        .form-content {
            margin-left: 12.5px;
            .item {
                height: auto;
                line-height: 47px;
                font-size: 14px;
                overflow: hidden;
            }
            .s-name {
                float: left;
            }
            .s-content {
                margin-left: 80px;
            }
            .st-name {
                line-height: 40px;
                width: 80%;
                border: none;
            }
        }
        .mid-line {}
        .ss-content {
            textarea {
                width: 95% !important;
                ;
                line-height: 20px !important;
                ;
                font-size: 14px !important;
                ;
                color: #000;
                height: 80px;
            }
        }
        .bottom-btn {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: auto;
            background:#fff;
        }
        .sub-btn {
            margin: 12.5px 17.5px;
            border-radius: 2px;
            font-size: 18px;
            line-height: 40px;
            height: 40px;
            color: #fff;
            background: #1d8fe1;
            clear: both;
        }
        .sub-btn:active  {
            background: #0f9ce4;
        }
        }
</style>



