<template>
    <section class="mask-page" v-if='showlock' @click="close">
        <div class="inner-box">
            <div class="d-line c-center content">
                <slot>
    
                </slot>
            </div>
            <div class="btn c-center">
                确定
            </div>
        </div>
    
    
    </section>
</template>

<script>
    export default {
        name: 'tips',
        components: {
    
        },
        props: {
            value: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                showlock: false
            };
        },
        watch: {
            showlock(val) {
                this.$emit("visible-change", val);
            },
            value(val) {
                console.log(val);
                this.showlock = val;
            }
    
        },
        created() {
            if (this.value) {
                this.showlock = true;
            }
        },
        methods: {
            close() {
                this.showlock = false;
                //hub.$emit('onEditComp', compName, compId, vm);
            },
            loadMore() {
    
            }
    
        }
    };
</script>

<style scoped lang='less'>
    .inner-box {
        position: relative;
        top: 50%;
        margin: 0 auto;
        margin-top: -70px;
        height: 140px;
        width: 295px;
        background: #fff;
    }
    
    .mask-page {
        position: fixed;
        height: 100%;
        z-index: #fff;
        left: 0;
        top: 0;
        overflow: auto;
        width: 100%;
        background: rgba(0, 0, 0, 0.8);
        .content {
            height: 91px;
            line-height: 91px;
        }
        .btn {
            line-height: 49px;
        }
    }
</style>



