</template>

<script>
    
    import Ccontent from './comps/content.vue';
    import cHeader from '@/components/head.vue';
    export default {
        components: {
            Ccontent,
             cHeader
        },
        data() {
            return {
                
            };
        },
        created() {
            
        },
        mounted(){
           
        },
        methods: {
            loadMore() {
               
            },
            send(){
                this.$http
                    .post('/hackathon/infer', {
                        id:111,
                        is_default: 111,
                        tg_default: 1
                    })
                    .then(err=>{
                          console.log(err);
                    });
            },
            getArticleList() {
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    }
                    else {
                        this.isNoMoreData = true;
                    }
                });
            }
        }
    };
</script>

