<template>
<div class="header">
    <div class="header-left" @click="leftClick()">
          <i  class="icon iconfont  icon-other_arrow_left"></i>
    </div>
    <div class="c-title c-center">
        <div class="c-line-clamp1">
            <slot></slot>
        </div>
    </div>
</div>
</template>

<script>

export default {
    name: 'backheader',
    props: ['left'],
    data() {
        return {
        };
    },
    methods: {

        /**
         * 左上角返回按钮，配置了left的值，则跳转到left，否则返回
         */
        leftClick() {
            if (this.left) {
                location.href = this.left;
                return;
            }
            history.back();
        },

        /**
         * 右上角按钮，会向父元素触发 rightClick 事件
         */
        rightClick() {
            this.$emit('rightClick');
        }
    }
};
</script>
<style lang="less" scoped>

.header{
     height:42px;
     background:url('../assets/img/headbj.png');
     background-size: 100% 100%;
     position:relative;
     .header-left{
         position:absolute;
         left:0;
         top:0;
         width:50px;
        line-height:40px;
         i{
             margin-left:17.5px;
             font-size:17px;
              color:#fff;
         }
     }
     .c-title{
         
         line-height:40px;
         color:#fff;
     }

}
</style>


